import { useState, useEffect, useCallback } from 'react';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';
import { Box, Breadcrumbs, Container, Grid, Link, Typography, } from '@material-ui/core';
// import { customerApi } from '../../__fakeApi__/customerApi';
import AppointmentsListTable from '../../../../components/dashboard/workshop/appointments/AppointmentsListTable';
import useMounted from '../../../../hooks/useMounted';
import ChevronRightIcon from '../../../../icons/ChevronRight';
// import DownloadIcon from '../../../icons/Download';
// import PlusIcon from '../../../../icons/Plus';
// import UploadIcon from '../../../icons/Upload';
import useSettings from '../../../../hooks/useSettings';
import gtm from '../../../../lib/gtm';
import firebase from '../../../../lib/firebase';
import useAuth from '../../../../hooks/useAuth';
import avatar from '../../../../assets/images/Mac/logo.jpg';

const AppointmentsList = () => {
  const mounted = useMounted();
  const { settings } = useSettings();
  const [customers, setCustomers] = useState([]);
  const { user } = useAuth();
  const navigate = useNavigate();

  const getActives = () => {
    firebase
      .firestore()
      .collection('workshop_appointments')
      .get()
      .then((snapshot) => {
        const data = [];
        snapshot.forEach((doc) => {
          /* eslint-disable object-shorthand */
          data.push({ ...doc.data(), id: doc.id, avatar: avatar });
        });
        console.log(data);
        setCustomers(data);
      });
  };

  useEffect(() => {
    firebase
      .firestore()
      .collection('staff')
      .doc(user.id)
      .get()
      .then((snap) => {
        if (snap.data().rank.id === 'dealership_agent') {
          navigate('/dashboard');
        }
      });
    getActives();
    gtm.push({ event: 'page_view' });
  }, []);

  const getCustomers = useCallback(async () => {
    try {
      // const data = await customerApi.getCustomers();

      // if (mounted.current) {
      //   setCustomers(data);
      // }
      setCustomers([]);
    } catch (err) {
      console.error(err);
    }
  }, [mounted]);

  useEffect(() => {
    getCustomers();
  }, [getCustomers]);

  return (
    <>
      <Helmet>
        <title>Dashboard: Citas | Mac Admin</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: 'background.default',
          minHeight: '100%',
          py: 8
        }}
      >
        <Container maxWidth={settings.compact ? 'xl' : false}>
          <Grid
            container
            justifyContent="space-between"
            spacing={3}
          >
            <Grid item>
              <Typography
                color="textPrimary"
                variant="h5"
              >
                Lista de Citas
              </Typography>
              <Breadcrumbs
                aria-label="breadcrumb"
                separator={<ChevronRightIcon fontSize="small" />}
                sx={{ mt: 1 }}
              >
                <Link
                  color="textPrimary"
                  component={RouterLink}
                  to="/dashboard"
                  variant="subtitle2"
                >
                  Dashboard
                </Link>
                <Typography
                  color="textSecondary"
                  variant="subtitle2"
                >
                  Lista de Citas
                </Typography>
              </Breadcrumbs>
              {/* <Box
                sx={{
                  mb: -1,
                  mx: -1,
                  mt: 1
                }}
              >
                <Button
                  color="primary"
                  startIcon={<UploadIcon fontSize="small" />}
                  sx={{ m: 1 }}
                >
                  Import
                </Button>
                <Button
                  color="primary"
                  startIcon={<DownloadIcon fontSize="small" />}
                  sx={{ m: 1 }}
                >
                  Export
                </Button>
              </Box> */}
            </Grid>
            {/* <Grid item>
              <Box sx={{ m: -1 }}>
                <Button
                  color="primary"
                  startIcon={<PlusIcon fontSize="small" />}
                  sx={{ m: 1 }}
                  component={RouterLink}
                  variant="contained"
                  to="/dashboard/concesionario/vehiculos/crear"
                >
                  Agregar VehÃ­culo
                </Button>
              </Box>
            </Grid> */}
          </Grid>
          <Box sx={{ mt: 3 }}>
            <AppointmentsListTable customers={customers} />
          </Box>
        </Container>
      </Box>
    </>
  );
};

/* eslint-disable eol-last */
export default AppointmentsList;